import $ from "jquery";
import { Modal } from "bootstrap";

import { getInstSources, updateInstSources } from "./instSourceList";
import getSources from "../background/sources/getSources";

import { BIBCNRS_SOURCE_PREFIX } from "../lib/constants";

export const openBibDomainModal = () => {
  loadBibDomains();
  const bibDomainsModal = new Modal(document.getElementById("bibDomainsModal")); // BibCnrs domains selection
  bibDomainsModal.show();
};

export const initBibDomainModal = () => {
  buildBibDomForm();
  loadBibDomains();
  validateBibDomains();
};

const getBibDomainsCheckboxes = () =>
  Array.from(
    document.querySelectorAll(
      ".bibDomainsModalBody > .form-check > .form-check-input"
    )
  ); // Get all the checkboxes in the modal

const buildBibDomCheckInput = ({ id, name }) => {
  const bibDomCheckForm = document.createElement("div");
  bibDomCheckForm.className = "form-check";
  const checkboxInput = document.createElement("input");
  checkboxInput.type = "checkbox";
  checkboxInput.className = "form-check-input";
  checkboxInput.value = name;
  checkboxInput.id = id;
  bibDomCheckForm.appendChild(checkboxInput);
  const checkboxLabel = document.createElement("label");
  checkboxLabel.className = "form-check-label";
  checkboxLabel.htmlFor = id;
  checkboxLabel.textContent = name;
  bibDomCheckForm.appendChild(checkboxLabel);
  return bibDomCheckForm;
}; // Build BibCnrs domain list to display inside Modal

const buildBibDomForm = () => {
  const { bibSources = [] } = getSources(); // Get source list
  bibSources.map(({ name, id }) =>
    $(".bibDomainsModalBody").append(buildBibDomCheckInput({ id, name }))
  ); // Insert BibCnrs domain list inside modal body
  const checkBoxes = getBibDomainsCheckboxes();
  for (const checkBox of checkBoxes) {
    checkBox.addEventListener("change", (e) => {
      const anyChecked =
        checkBoxes.filter((checkBox) => checkBox.checked).length > 0;
      document.getElementById("bibDomainsWarning").hidden = anyChecked;
      document.getElementById("bibDomainsModalValidateBtn").disabled =
        !anyChecked;
    });
  } // Add change event listener to checkboxes
};

const loadBibDomains = () => {
  const currBibSourceIds = getInstSources()
    .filter((currSource) => currSource.id.startsWith(BIBCNRS_SOURCE_PREFIX))
    .map((currBibSource) => currBibSource.id);
  getBibDomainsCheckboxes().map((checkBox) => {
    if (currBibSourceIds.includes(checkBox.id)) {
      document.getElementById("bibDomainsWarning").hidden = true;
      document.getElementById("bibDomainsModalValidateBtn").disabled = false;
      checkBox.checked = true;
    } else checkBox.checked = false;
  }); // Set the BibDomain checkboxes with previously selected ones
};

const getCheckedBibDomains = () =>
  getBibDomainsCheckboxes()
    .filter((checkBox) => checkBox.checked)
    .map(({ id, value }) => ({ id, name: value }));

const validateBibDomains = () => {
  $("#bibDomainsModalValidateBtn").on("click", () => {
    const checkedBibDomains = getCheckedBibDomains();
    const currInstSources = getInstSources();
    const updatedInstSources = currInstSources.filter(
      (currSource) =>
        !(
          currSource.id.startsWith(BIBCNRS_SOURCE_PREFIX) &&
          !checkedBibDomains.some(
            (checkedBibDomain) => currSource.id === checkedBibDomain.id
          )
        )
    ); // Filter unchecked bibDomains from current sources
    const updateBibSources = checkedBibDomains.filter(
      (checkedBibDomain) =>
        !currInstSources.some(
          (currInstSource) => currInstSource.id === checkedBibDomain.id
        )
    ); // Filter already added bibDomains in current sources
    updateInstSources(updatedInstSources.concat(updateBibSources));
  });
};
