import $ from "jquery";
import { Tooltip, Alert } from "bootstrap";
import Sortable from "sortablejs";

import "../assets/css/options.scss"; // Load Styles
import {
  activateSourceLibOnGS,
  hasGScholarSource,
  readGScholarActive,
  saveGScholarActive,
} from "./gScholarLib";
import {
  readInstSources,
  saveInstSources,
  updateInstSources,
} from "./instSourceList";
import {
  getInstitutions,
  updateInstitutions,
  instSelect,
  readInstitutions,
  saveInstitutions,
  initSelectInst,
} from "./institutionList";
import { initBibDomainModal } from "./bibDomains";
import { initDataCollectModal } from "./dataCollect";
import { currentBrowser } from "../lib/constants";

const buildAlert = (message, type, placeholder) => {
  const alert = document.getElementById(placeholder);
  const alertWrapper = document.createElement("div");
  alertWrapper.classList.add("alert", `alert-${type}`, "fw-bold");
  alertWrapper.setAttribute("role", "alert");
  const alertIcon = document.createElement("i");
  alertIcon.classList.add("bi", "bi-check-circle", "fs-5", "m-3");
  alertWrapper.appendChild(alertIcon);
  alertWrapper.insertAdjacentText("beforeend", message);
  alert.appendChild(alertWrapper);
  return alertWrapper;
};

const initOptions = async () => {
  const domIds = [
    "optionsPageTitle",
    "selectInst",
    "contactUsDesc",
    "assistantTeam",
    "saveOptionsBtn",
    "selectInstWarning",
    "googleScholarConfig",
    "selectSource",
    "serverErrorMsg",
    "bibDomainsModalTitle",
    "bibDomainsWarning",
    "bibDomainsModalValidateBtn",
    "dataCollectModalTitle",
    "dataCollectModalAgreeBtn",
    "dataCollectModalDeclineBtn",
    "resetOptionsBtn",
  ];
  for (const domId of domIds) {
    document.getElementById(domId).textContent =
      currentBrowser.i18n.getMessage(domId);
  } // Insert translated text into options page
  document.getElementById("saveOptionsBtn").title = "";
  document.getElementById("resetOptionsBtn").title = "";
  document.getElementById(
    "assistantTeam"
  ).href = `mailto:clickandread@inist.fr?subject=${encodeURIComponent(
    currentBrowser.i18n.getMessage("supportMailSubject")
  )}&body=${encodeURIComponent(
    currentBrowser.i18n.getMessage("supportMailBody")
  )}`; // Set support team contact mail link
  $("#addonVersion").append(
    `Version ${currentBrowser.runtime.getManifest().version}`
  );
  initSelectInst(); // Init Select institution
  initBibDomainModal(); // Build the bibDomains' Modal
  new Sortable.create(document.getElementById("sourceList")); // Source list sortable
  // Activate the tooltips
  new Tooltip(document.querySelector("#inst-container > i"), {
    title: currentBrowser.i18n.getMessage("selectInstDesc"),
  });
  new Tooltip(document.querySelector("#source-container > i"), {
    title: currentBrowser.i18n.getMessage("selectSourceDesc"),
  });
  new Tooltip(document.querySelector("#gs-container > div > i"), {
    title: currentBrowser.i18n.getMessage("activateGSDesc"),
  });
  // Options' settings reset event listener
  document.getElementById("resetOptionsBtn").addEventListener("click", (e) => {
    e.preventDefault();
    instSelect.val(null).trigger("change");
    document.getElementById("instGroup")?.replaceChildren();
    document.getElementById("sourceList")?.replaceChildren();
    updateInstSources();
    $("#enableGS").prop("checked", false);
    document.getElementById("selectInstWarning").hidden = true;
    document.getElementById("source-container").hidden = true;
    document.getElementById("gs-container").hidden = true;
    document.getElementById("bibDomainsWarning").hidden = false;
    document.getElementById("bibDomainsModalValidateBtn").disabled = true;
  });
  // Load Options' settings from local storage
  const savedInstitutions = await readInstitutions();
  const savedSources = await readInstSources();
  const gScholar = await readGScholarActive();
  if (savedInstitutions) {
    updateInstitutions(savedInstitutions); // Populate saved institution list
    // Populate saved source list
    if (savedSources) {
      updateInstSources(savedSources);
      document.getElementById("source-container").hidden = false;
      if (gScholar) $("#enableGS").prop("checked", true);
      document.getElementById("gs-container").hidden = false;
    }
  } else updateInstSources(); // Init source list with default sources
  initDataCollectModal(); // Onboarding modal for anonymous data collection
};

const saveOptions = async () => {
  const institutions = getInstitutions();
  document.getElementById("selectInstWarning").hidden = institutions.length > 0; // User must select at least one institution
  if (institutions.length > 0) {
    const gsEnabled = hasGScholarSource() && $("#enableGS").is(":checked");
    if (gsEnabled) activateSourceLibOnGS(); // Activate sources' GScholar Libs if checkbox enabled
    await saveGScholarActive(gsEnabled);
    await saveInstSources();
    await saveInstitutions();
    const saveAlert = new Alert(
      buildAlert(
        currentBrowser.i18n.getMessage("savedAlertMsg"),
        "success",
        "saveAlert"
      )
    );
    setTimeout(() => {
      saveAlert.close();
    }, 3000);
  }
};

$(() => {
  initOptions(); // Init Options page
  $("#saveOptionsBtn").on("click", () => saveOptions()); // Save options in browser local storage
});
